.image-compare-wrapper {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;


  .image-compare-wrapper-title {
    font-size: 20px;
    font-weight: 600;
    color: #555;
    margin-bottom: 24px;
    line-height: 1.4;
    text-align: left;
  }

  .image-compare-wrapper-description {
    font-size: 14px;
    line-height: 1.6;
    color: #666;
    text-align: left;
    margin: 0;
  }

}


.aligners-container {
  flex: 1;
  position: relative;
  height: 300px;
  background: linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%);
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;

  .aligner {
    position: absolute;
    width: 200px;
    height: 120px;
    background: linear-gradient(135deg, rgba(255, 255, 255, 0.9) 0%, rgba(255, 255, 255, 0.7) 100%);
    border: 2px solid rgba(71, 211, 207, 0.3);
    border-radius: 60px 60px 20px 20px;
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.1);

    &.aligner-bottom {
      bottom: 60px;
      left: 50px;
      transform: rotate(-5deg);
    }

    &.aligner-top {
      top: 60px;
      right: 50px;
      transform: rotate(5deg);
    }
  }

  .arrow {
    position: absolute;
    width: 80px;
    height: 80px;
    border: 4px solid #47d3cf;
    border-top: none;
    border-right: none;
    transform: rotate(-45deg);
    left: 120px;
    bottom: 100px;
    z-index: 2;

    &::before {
      content: '';
      position: absolute;
      top: -8px;
      left: -8px;
      width: 0;
      height: 0;
      border-left: 12px solid transparent;
      border-right: 12px solid transparent;
      border-bottom: 12px solid #47d3cf;
      transform: rotate(45deg);
    }
  }

  .sparkles {
    position: absolute;
    width: 100%;
    height: 100%;

    .sparkle {
      position: absolute;
      width: 8px;
      height: 8px;
      background: #47d3cf;
      border-radius: 50%;
      animation: sparkle 2s ease-in-out infinite;

      &::before {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        width: 14px;
        height: 2px;
        background: #47d3cf;
        transform: translate(-50%, -50%);
        border-radius: 1px;
      }

      &::after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        width: 2px;
        height: 14px;
        background: #47d3cf;
        transform: translate(-50%, -50%);
        border-radius: 1px;
      }

      &.sparkle-1 {
        top: 20%;
        left: 20%;
        animation-delay: 0s;
      }

      &.sparkle-2 {
        top: 30%;
        right: 25%;
        animation-delay: 0.4s;
      }

      &.sparkle-3 {
        bottom: 30%;
        left: 30%;
        animation-delay: 0.8s;
      }

      &.sparkle-4 {
        bottom: 20%;
        right: 20%;
        animation-delay: 1.2s;
      }

      &.sparkle-5 {
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        animation-delay: 1.6s;
      }
    }
  }
}